import { Modal } from "flowbite-react";
import { cn } from "../../utils/custom_functions";
import { useMutation, useQueryClient } from "react-query";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { FLOORS_ARRAY, HELPERS_ARRAY } from "../../utils/information";
import { updateBookingFunction } from "../../api/api";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { Button } from "../reusable_components/custom_button";
import { ThreeDots } from "react-loader-spinner";
import { useTimeout } from "../../utils/hooks/use_time_out";

export function UpdateAdditionalInformation({
  booking,
  openUpdateAdditionalInformationModal,
  setOpenUpdateAdditionalInformationModal,
  ...props
}) {
  const [isUpdateError, setIsUpdateError] = useState(false);
  const [isUpdateSucces, setIsUpdateSucces] = useState(false);
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const queryClient = useQueryClient();
  const { register, handleSubmit } = useForm({
    defaultValues: {
      helpers: booking?.helpers,
      floors: booking?.floors,
      payment_option: booking?.payment_option,
      customer_driver_note: booking?.customer_driver_note,
    },
  });
  const { set } = useTimeout(() => {
    setIsUpdateError(() => false);
    setIsUpdateSucces(() => false);
    setOpenUpdateAdditionalInformationModal(() => false);
  }, 3000);

  const {
    isLoading: isUpdateLoading,
    mutateAsync: updateBookingAsync,
    error: updateError,
  } = useMutation("update-additional-information-modal", {
    mutationFn: updateBookingFunction,
    onSuccess: (event) => {
      setIsUpdateSucces((_) => true);
      setDisableUserInterface((s) => false);
      queryClient.invalidateQueries(["fetched-booking"]);
      set();
    },
    onMutate: (event) => {
      setDisableUserInterface((l) => true);
    },
    onError: (event) => {
      setIsUpdateError((_) => true);
      setDisableUserInterface((e) => false);
    },
  });
  const onSubmit = async (data) => {
    try {
      await updateBookingAsync({ id: booking.id, ...data });
    } catch (error) {}
  };
  return (
    <Modal
      {...props}
      size="lg"
      id="update-additional-information"
      position="center"
      show={openUpdateAdditionalInformationModal}
      onClose={() => setOpenUpdateAdditionalInformationModal(false)}
    >
      <Modal.Header
        className={cn("bg-yellow-200 border-none", {
          "bg-gray-200": booking?.booking_completed,
          "bg-red-200": booking?.booking_canceled,
        })}
      >
        <span className="text-slate-800 font-bold text-2xl">add info</span>
      </Modal.Header>
      <Modal.Body className="bg-white rounded-md" color="#fff">
        <div className="mb-6">
          <p className="text-sm">
            Trust Reload logistics services to transport your items safely.
          </p>
        </div>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="w-full">
            <select
              {...register("helpers", {
                valueAsNumber: true,
              })}
              id="helpers"
              name="helpers"
              disabled={disableUserInterface}
              className="block text-sm appearance-none w-full border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-md leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400  disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-75"
            >
              {HELPERS_ARRAY.map((_, i) => (
                <option key={i + 1} value={i + 1}>
                  {i + 1} Helpers
                </option>
              ))}
            </select>
            <span className="text-xs text-gray-400">
              Request additional suport
            </span>
          </div>
          <div>
            <select
              {...register("floors", {
                valueAsNumber: true,
              })}
              id="floors"
              name="floors"
              disabled={disableUserInterface}
              className="mt-4 block text-sm appearance-none w-full  border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-md leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400  disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-75"
            >
              {FLOORS_ARRAY.map((_, i) => (
                <option key={i} value={i + 1}>
                  {i + 1} Floors
                </option>
              ))}
            </select>
            <span className="text-xs text-gray-400">
              Total number of floors to carry up or down in both pick and
              dropoff locations
            </span>
          </div>
          <div>
            <select
              {...register("payment_option")}
              id="payment_option"
              name="payment_option"
              disabled={disableUserInterface}
              className="mt-4 block text-sm appearance-none w-full border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-md leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400  disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-75"
            >
              {["EFT", "CASH"].map((paymentOption, i) => (
                <option key={i} value={paymentOption}>
                  {paymentOption} payment
                </option>
              ))}
            </select>
            <span className="text-xs text-gray-400">
              Choose your prefared payment option
            </span>
          </div>
          <div className="mt-4">
            <textarea
              {...register("customer_driver_note")}
              type="text"
              rows={3}
              name="customer_driver_note"
              placeholder="Leave a note for the driver"
              disabled={disableUserInterface}
              className={cn(
                "text-sm text-gray-700 w-full rounded-md border-gray-200 focus:bg-white focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none  disabled:bg-gray-200"
              )}
            />
            <span className="text-xs text-gray-400">
              Need to give the driver additional instruction?, leave a note for
              the driver here.
            </span>
          </div>
          <div>
            {isUpdateError && (
              <ErrorAlert
                className="mb-4"
                secondaryText={`${updateError.response.data.message} ${updateError.message}`}
              />
            )}
            {isUpdateSucces && (
              <SuccessAlert
                secondaryText={`Additional information successfully updated.`}
                className="my-4 w-full"
              />
            )}
          </div>

          <div className="flex flex-row items-center justify-end gap-3">
            <Button disabled={disableUserInterface} className="w-auto">
              {isUpdateLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                "Update"
              )}
            </Button>
          </div>
        </form>
      </Modal.Body>
    </Modal>
  );
}
