import { Modal } from "flowbite-react";
import { cn } from "../../utils/custom_functions";
import { useEffect, useRef, useState } from "react";
import { useMutation, useQueryClient } from "react-query";
import { updateBookingFunction } from "../../api/api";
import { ChooseVehicleTypeCard } from "../reusable_components/create_booking/choose_vehicle_type_card";
import { VEHICLE_TYPES } from "../../utils/information";
import { Button } from "../reusable_components/custom_button";
import { ThreeDots } from "react-loader-spinner";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { useTimeout } from "../../utils/hooks/use_time_out";

export function UpdateVehicleTypeModal({
  booking,
  openUpdateVehicleTypeModal,
  setOpenUpdateVehicleTypeModal,
  ...props
}) {
  const [isUpdateError, setIsUpdateError] = useState(false);
  const [isUpdateSucces, setIsUpdateSucces] = useState(false);
  const [choosenVehicleType, setChoosenVehicleType] = useState(
    Number(booking?.vehicle_type).toFixed(1)
  );
  const queryClient = useQueryClient();
  const { set } = useTimeout(() => {
    setIsUpdateError(() => false);
    setIsUpdateSucces(() => false);
    setOpenUpdateVehicleTypeModal(() => false);
  }, 3000);
  const [disableUserInterface, setDisableUserInterface] = useState(false);

  const {
    isLoading: isUpdateLoading,
    isSuccess: success,
    mutateAsync: upadateBookingAsync,
    isError: error,
    error: updateError,
  } = useMutation("update-vehicle-type", {
    mutationFn: updateBookingFunction,
    onSuccess: (event) => {
      setIsUpdateSucces((_) => true);
      setDisableUserInterface((s) => false);
      queryClient.invalidateQueries(["fetched-booking"]);
      set();
    },
    onMutate: (event) => {
      setDisableUserInterface((l) => true);
    },
    onError: (event) => {
      setIsUpdateError((_) => true);
      setDisableUserInterface((e) => false);
    },
  });

  const handleUpdate = async () => {
    try {
      await upadateBookingAsync({
        vehicle_type: Number(choosenVehicleType).toFixed(1),
        id: booking?.id,
      });
    } catch (error) {}
  };

  return (
    <Modal
      {...props}
      size="lg"
      id="vehicle-type"
      position="center"
      show={openUpdateVehicleTypeModal}
      onClose={() => setOpenUpdateVehicleTypeModal(false)}
    >
      <Modal.Header
        className={cn("bg-yellow-200 border-none", {
          "bg-gray-200": booking?.booking_completed,
          "bg-red-200": booking?.booking_canceled,
        })}
      >
        <span className="text-slate-800 font-bold text-2xl">Vehicle type</span>
      </Modal.Header>
      <Modal.Body className="bg-white rounded-md" color="#fff">
        <div className="mb-6">
          <p className="text-sm">
            Trust Reload logistics services to transport your items safely.
          </p>
        </div>
        {VEHICLE_TYPES.map((vehicle_detail, key) => (
          <ChooseVehicleTypeCard
            key={key}
            vehicle_details={vehicle_detail}
            onChange={setChoosenVehicleType}
            value={choosenVehicleType}
          />
        ))}

        <div className="mt-4 w-full flex flex-col justify-center items-end">
          {isUpdateError && (
            <ErrorAlert
              className="mb-4 w-full"
              secondaryText={`${updateError.response.data.message} ${updateError.message}`}
            />
          )}
          {isUpdateSucces && (
            <SuccessAlert
              secondaryText={`Vehicle type successfully updated.`}
              className="mb-4 w-full"
            />
          )}
        </div>
        <div className="w-full flex justify-end">
          <Button
            disabled={disableUserInterface}
            className="w-auto"
            onClick={handleUpdate}
          >
            {isUpdateLoading ? (
              <div className="w-full h-full flex justify-center items-center">
                <ThreeDots visible width="40" height="20" color="#000" />
              </div>
            ) : (
              "Update"
            )}
          </Button>
        </div>
      </Modal.Body>
    </Modal>
  );
}
