import { cn } from "../../../utils/custom_functions";

export function ChooseVehicleTypeCard({
  vehicle_details,
  onChange,
  value,
  ...props
}) {
  const onClick = () =>
    onChange((r) => Number(vehicle_details.vehicle_type).toFixed(1));
  return (
    <div
      className={cn(
        "rounded-md p-2 shadow-sm border border-gray-200 my-2 text-xs",
        {
          "text-sm bg-yellow-400 bg-opacity-40":
            value === Number(vehicle_details.vehicle_type).toFixed(1),
        }
      )}
      onClick={onClick}
    >
      <div className="flex justify-start items-start gap-3">
        <input
          {...props}
          type="checkbox"
          id={Number(vehicle_details.vehicle_type).toFixed(1)}
          value={Number(vehicle_details.vehicle_type).toFixed(1)}
          checked={value === Number(vehicle_details.vehicle_type).toFixed(1)}
          onChange={onClick}
          className={cn(
            "peer rounded-sm border-gray-300 my-[0.1rem] checked:my-[0.3rem] text-yellow-400 focus:ring-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
            {}
          )}
        />
        <div className="flex flex-col gap-2">
          <label
            htmlFor="termsandprivacy"
            className="peer-checked:font-extrabold font-bold peer-checked:text-yellow-500"
          >
            {vehicle_details.vehicle_type} Ton{" "}
            <span className="font-normal text-slate-800">
              {vehicle_details.vehicle_description}
            </span>
          </label>
          <div className="flex flex-row justify-start items-center gap-4">
            <img
              className="h-9"
              src={vehicle_details.vehicle_type_svg}
              alt="{vehicle_details.vehicle_type_svg}"
            />
            <div className="text-xs">
              <p className="font-extrabold text-slate-800">
                Max weight:{" "}
                <span className="font-normal">
                  {vehicle_details.vehicle_max_weight}
                </span>
              </p>
              <p className="font-extrabold text-slate-800">
                Load deck:{" "}
                <span className="font-normal">
                  {vehicle_details.vehicle_load_deck}
                </span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
