import { cn } from "../../../utils/custom_functions";
import { Tooltip } from "flowbite-react";
import moment from "moment";
import { FiEdit3 } from "react-icons/fi";
import { Pickupdropoff } from "./pickup_dropoff_content";
import { BsInfoCircle as InfoIcon } from "react-icons/bs";
import {
  BASE_AMOUNT,
  LOYAL_CUSTOMER_DISCOUNT,
  MID_MONTH_DISCOUNT,
  PRICE_ADJUSTMENT,
} from "../../../utils/information";

export function BookingViewContent({
  booking,
  setOpenUpdateRoutesModal,
  setOpenUpdateDateTimeModal,
  setOpenUpdateVehicleTypeModal,
  setOpenUpdateAdditionalInformationModal,
  ...props
}) {
  return (
    <table
      {...props}
      className={cn(
        "table-auto border border-yellow-100 mt-4 w-full border-separate border-spacing-0 rounded-md shadow-sm",
        {
          "border-red-100": booking?.booking_canceled,
          "border-gray-100": booking?.booking_completed,
        }
      )}
    >
      <thead
        className={cn("bg-yellow-200", {
          "bg-red-200": booking?.booking_canceled,
          "bg-gray-200": booking?.booking_completed,
        })}
      >
        <tr>
          <th className="rounded-ss-md text-start px-4 py-3">Description</th>
          <th className="rounded-tr-md text-start px-4 py-3">Details</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td className="px-4 py-3 text-sm">
            <div className="flex flex-row items-center justify-start gap-2">
              <span>Date, time </span>
              <div>
                <button
                  onClick={() => setOpenUpdateDateTimeModal(true)}
                  disabled={
                    booking?.booking_canceled || booking?.booking_completed
                  }
                  className={cn(
                    "text-yellow-400 disabled:text-gray-300 hover:text-yellow-500 hover:disabled:text-gray-300"
                  )}
                >
                  <FiEdit3 size={18} />
                </button>
              </div>
            </div>
          </td>
          <td className="px-4 py-3 text-sm">
            {moment(new Date(booking?.booking_date)).format("YYYY-MMM-DD")},{" "}
            {moment(
              new Date(`${booking?.booking_date} ${booking?.booking_time}`)
            ).format("HH:mm")}
          </td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">
            <div className="flex flex-row items-center justify-start gap-2">
              <span>Vehicle type </span>
              <div>
                <button
                  onClick={() => setOpenUpdateVehicleTypeModal(true)}
                  disabled={
                    booking?.booking_canceled || booking?.booking_completed
                  }
                  className={cn(
                    "text-yellow-400 disabled:text-gray-300 hover:text-yellow-500 hover:disabled:text-gray-300"
                  )}
                >
                  <FiEdit3 size={18} />
                </button>
              </div>
            </div>
          </td>
          <td className="px-4 py-3 text-sm">{booking?.vehicle_type} Ton</td>
        </tr>
        <tr>
          <td colSpan={2} className="px-4 text-sm font-bold pt-3">
            <div className="flex flex-row items-center justify-start gap-2">
              <span>Additional information </span>
              <div>
                <button
                  onClick={() => setOpenUpdateAdditionalInformationModal(true)}
                  disabled={
                    booking?.booking_canceled || booking?.booking_completed
                  }
                  className={cn(
                    "text-yellow-400 disabled:text-gray-300 hover:text-yellow-500 hover:disabled:text-gray-300"
                  )}
                >
                  <FiEdit3 size={18} />
                </button>
              </div>
            </div>
          </td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">Helpers</td>
          <td className="px-4 py-3 text-sm">{booking?.helpers}</td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">Floors</td>
          <td className="px-4 py-3 text-sm">{booking?.floors}</td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm">Payment option</td>
          <td className="px-4 py-3 text-sm">{booking?.payment_option}</td>
        </tr>
        {String(booking?.customer_driver_note).length > 0 && (
          <tr>
            <td colSpan={2} className="px-4 py-3 text-sm">
              <div>
                <p>Drivers note:</p>
                <p className="text-gray-400">{booking?.customer_driver_note}</p>
              </div>
            </td>
          </tr>
        )}
        <tr>
          <td colSpan={2} className="px-4 py-3 text-sm">
            <div>
              <div className="py-2 w-full flex justify-start text-sm font-bold">
                <div className="flex flex-row items-start justify-start gap-2">
                  <div className="flex flex-col gap-2">
                    <p>Pick up and drop off locations</p>
                  </div>

                  <div>
                    <button
                      onClick={() => setOpenUpdateRoutesModal(true)}
                      disabled={
                        booking?.booking_canceled || booking?.booking_completed
                      }
                      className={cn(
                        "text-yellow-400 disabled:text-gray-300 hover:text-yellow-500 hover:disabled:text-gray-300"
                      )}
                    >
                      <FiEdit3 size={18} />
                    </button>
                  </div>
                </div>
              </div>
              <div className="mt-1">
                {booking?.pickup_dropoff_routes.map(
                  (pickup_dropoff_routes, i) => {
                    return (
                      <Pickupdropoff
                        key={i}
                        pickup_dropoff_route={pickup_dropoff_routes}
                      />
                    );
                  }
                )}
              </div>
              <div className="w-full flex justify-start pt-2">
                <p className="font-normal text-sm text-gray-400">
                  Total distance{" "}
                  <span className="text-slate-800">
                    {booking?.distance} kms
                  </span>
                </p>
              </div>
            </div>
          </td>
        </tr>
        <tr>
          <td
            className={cn(
              "px-4 py-3 text-sm border-t border-t-yellow-100 flex flex-row justify-start items-center gap-1",
              {
                "border-t-red-100": booking?.booking_canceled,
                "border-t-gray-100": booking?.booking_completed,
              }
            )}
          >
            Base price{" "}
            <Tooltip content={BASE_AMOUNT} placement="top" trigger="click">
              <InfoIcon className="text-yellow-400 hover:text-yellow-500" />
            </Tooltip>
          </td>
          <td className="px-4 py-3 text-sm border-t border-t-yellow-100 ">
            R {booking?.base_amount}{" "}
          </td>
        </tr>
        <tr className="border-b">
          <td className="px-4 py-3 text-sm flex flex-row justify-start items-center gap-1">
            Mid month discount{" "}
            <Tooltip
              content={MID_MONTH_DISCOUNT}
              placement="top"
              trigger="click"
            >
              <InfoIcon className="text-yellow-400 hover:text-yellow-500" />
            </Tooltip>
          </td>
          <td className="px-4 py-3 text-sm text-red-500">
            R -{booking?.mid_month_discount}{" "}
          </td>
        </tr>
        <tr>
          <td className="px-4 py-3 text-sm flex flex-row justify-start items-center gap-1">
            Loyal discount
            <Tooltip
              className="bg-slate-800"
              content={LOYAL_CUSTOMER_DISCOUNT}
              placement="top"
              trigger="click"
              arrow={true}
            >
              <InfoIcon className="text-yellow-400 hover:text-yellow-500" />
            </Tooltip>
          </td>
          <td className="px-4 py-3 text-sm text-red-500 ">
            R -{booking?.loyal_customer_discount}{" "}
          </td>
        </tr>
        {booking?.price_adjustment != 0 && (
          <tr>
            <td className="px-4 py-3 text-sm flex flex-col justify-start items-start gap-0">
              <div className="flex flex-row justify-start items-center gap-1">
                Price adjustment
                <Tooltip
                  className="bg-slate-800"
                  content={PRICE_ADJUSTMENT}
                  placement="top"
                  trigger="click"
                  arrow={true}
                >
                  <InfoIcon className="text-yellow-400 hover:text-yellow-500" />
                </Tooltip>
              </div>
              <Tooltip
                className="bg-slate-800"
                content={`Justification: ${booking?.price_adjustment_note}`}
                placement="top"
                trigger="click"
                arrow={true}
              >
                <span className="text-xs text-yellow-400 hover:text-yellow-600 cursor-pointer">
                  Read more
                </span>
              </Tooltip>
            </td>
            <td
              className={cn("px-4 py-3 text-sm text-red-500 ", {
                "text-green-500": booking?.price_adjustment > 0,
              })}
            >
              R {booking?.price_adjustment}
            </td>
          </tr>
        )}
        <tr>
          <td
            className={cn(
              "px-4 py-3 text-lg  bg-yellow-200 font-extrabold flex flex-row justify-start items-center gap-2",
              {
                "bg-red-200": booking?.booking_canceled,
                "bg-gray-200": booking?.booking_completed,
              }
            )}
          >
            Balance due
          </td>
          <td
            className={cn("px-4 py-3 text-lg font-extrabold bg-yellow-200", {
              "bg-red-200": booking?.booking_canceled,
              "bg-gray-200": booking?.booking_completed,
            })}
          >
            R {booking?.amount_due_customer}{" "}
          </td>
        </tr>
      </tbody>
    </table>
  );
}
