import { cn } from "../../utils/custom_functions";
import { BookingViewContent } from "./booking_content/booking_view_content";

export function BookingViewCard({
  booking,
  setOpenUpdateVehicleTypeModal,
  setOpenUpdateRoutesModal,
  setOpenUpdateDateTimeModal,
  setOpenUpdateAdditionalInformationModal,
  ...props
}) {
  return (
    <div className="rounded-md pb-6">
      <h2
        className={cn("text-start text-slate-800 font-extrabold text-lg", {
          "text-red-600": booking?.booking_canceled,
          "text-gray-800": booking?.booking_completed,
        })}
      >
        {booking?.id > 0 && (
          <div className="pt-2">
            <span>
              Invoice {booking?.id}{" "}
              {booking?.booking_canceled && <span>cancelled</span>}
              {booking?.booking_completed && <span>completed</span>}
            </span>
          </div>
        )}
      </h2>

      <BookingViewContent
        booking={booking}
        setOpenUpdateRoutesModal={setOpenUpdateRoutesModal}
        setOpenUpdateDateTimeModal={setOpenUpdateDateTimeModal}
        setOpenUpdateVehicleTypeModal={setOpenUpdateVehicleTypeModal}
        setOpenUpdateAdditionalInformationModal={
          setOpenUpdateAdditionalInformationModal
        }
      />

      {/*Edit booking*/}
      {props.children}
      {/*Edit booking*/}
    </div>
  );
}
