import { useMutation, useQuery, useQueryClient } from "react-query";
import { useParams } from "react-router";
import { fetchBookingFunction, updateBookingFunction } from "../../api/api";
import { LoadingComponent } from "../reusable_components/loading-content";
import { ErrorAlert } from "../reusable_components/alerts/alerts";
import { Button } from "../reusable_components/custom_button";
import { useEffect, useState } from "react";
import { ThreeDots } from "react-loader-spinner";
import { cn } from "../../utils/custom_functions";
import { LoadPageAnimation } from "../reusable_components/load_page_animation";
import { BookingViewCard } from "../reusable_components/booking_view_card";
import { UpdateVehicleTypeModal } from "../modals/update_vehicle_type_modal";
import { UpdateAdditionalInformation } from "../modals/update_additional_information_modal";
import { UpdateRoutesModal } from "../modals/update_routes_modal";
import { UpdateDateTimeModal } from "../modals/update_date_time";

export default function ViewBooking() {
  const [openUpdateVehicleTypeModal, setOpenUpdateVehicleTypeModal] =
    useState(false);
  const [
    openUpdateAdditionalInformationModal,
    setOpenUpdateAdditionalInformationModal,
  ] = useState(false);
  const [openUpdateDateTimeModal, setOpenUpdateDateTimeModal] = useState(false);
  const [openUpdateRoutesModal, setOpenUpdateRoutesModal] = useState(false);
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const queryClient = useQueryClient();

  const { id } = useParams();

  const {
    data: booking,
    error: bookingErrors,
    isError: isBookingError,
    isLoading: isBookingLoading,
    // isSuccess: isBookingSuccess,
  } = useQuery({
    queryKey: ["fetched-booking"],
    queryFn: () => fetchBookingFunction(id),
    onSuccess: (data) => {
      // console.log(data);
    },
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const {
    isLoading: isCancelLoading,
    // isSuccess: isCancelSucces,
    mutateAsync: cancelBookingAsync,
    isError: isCancelError,
    error: cancelError,
  } = useMutation("cancel-booking", {
    mutationFn: updateBookingFunction,
    onSuccess: (event) => {
      window.scrollTo(0, 0);
      setDisableUserInterface((s) => false);
      queryClient.invalidateQueries(["fetched-booking"]);
    },
    onMutate: (event) => {
      setDisableUserInterface((l) => true);
    },
    onError: (event) => {
      setDisableUserInterface((e) => false);
    },
  });

  const handleCancelation = async () => {
    try {
      await cancelBookingAsync({ booking_canceled: true, id: booking.id });
    } catch (error) {}
  };

  if (isBookingLoading) {
    return <LoadingComponent secondary_text={"loading booking"} />;
  }

  if (isBookingError) {
    return <ErrorAlert secondaryText={`${bookingErrors.response.data}`} />;
  }

  return (
    <LoadPageAnimation>
      <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
        <div className="rounded-md p-4 shadow-sm border border-gray-200 my-2">
          <h2
            className={cn("text-start text-black font-extrabold text-3xl", {
              "text-red-500": booking?.booking_canceled,
              "text-gray-800": booking?.booking_completed,
            })}
          >
            Invoice {id}
            <p className="text-sm font-normal text-slate-800 pt-2">
              Thank you for trusting Reload Logistic Services with your personal
              information.
            </p>
          </h2>
        </div>

        <BookingViewCard
          key={booking.id}
          booking={booking}
          setOpenUpdateVehicleTypeModal={setOpenUpdateVehicleTypeModal}
          setOpenUpdateRoutesModal={setOpenUpdateRoutesModal}
          setOpenUpdateDateTimeModal={setOpenUpdateDateTimeModal}
          setOpenUpdateAdditionalInformationModal={
            setOpenUpdateAdditionalInformationModal
          }
        >
          <div>
            {isCancelError && (
              <ErrorAlert
                className="mb-4"
                secondaryText={`${cancelError.response.data.message} ${cancelError.message}`}
              />
            )}

            <div className="flex flex-row justify-end gap-4 mt-4">
              <Button
                onClick={handleCancelation}
                disabled={
                  booking.booking_canceled ||
                  booking.booking_completed ||
                  disableUserInterface
                }
                className="w-auto bg-red-500 text-white hover:bg-red-600 disabled:bg-red-200 disabled:text-gray-600"
              >
                {isCancelLoading ? (
                  <div className="w-full h-full flex justify-center items-center">
                    <ThreeDots visible width="40" height="20" color="#000" />
                  </div>
                ) : (
                  "Cancel"
                )}
              </Button>
            </div>
          </div>
        </BookingViewCard>
        {/*modals*/}
        <UpdateVehicleTypeModal
          booking={booking}
          openUpdateVehicleTypeModal={openUpdateVehicleTypeModal}
          setOpenUpdateVehicleTypeModal={setOpenUpdateVehicleTypeModal}
        />
        <UpdateRoutesModal
          booking={booking}
          openUpdateRoutesModal={openUpdateRoutesModal}
          setOpenUpdateRoutesModal={setOpenUpdateRoutesModal}
        />
        <UpdateDateTimeModal
          booking={booking}
          openUpdateDateTimeModal={openUpdateDateTimeModal}
          setOpenUpdateDateTimeModal={setOpenUpdateDateTimeModal}
        />
        <UpdateAdditionalInformation
          booking={booking}
          openUpdateAdditionalInformationModal={
            openUpdateAdditionalInformationModal
          }
          setOpenUpdateAdditionalInformationModal={
            setOpenUpdateAdditionalInformationModal
          }
        />
        {/*modals*/}
        <div className="mb-20" />
      </main>
    </LoadPageAnimation>
  );
}
