import { Modal } from "flowbite-react";
import { cn } from "../../utils/custom_functions";
import { Controller, useForm } from "react-hook-form";
import DatePicker from "react-datepicker";
import { useState } from "react";
import moment from "moment";
import "../../css/date_time.css";
import { Button } from "../reusable_components/custom_button";
import { useMutation, useQueryClient } from "react-query";
import { updateBookingFunction } from "../../api/api";
import { ThreeDots } from "react-loader-spinner";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { useTimeout } from "../../utils/hooks/use_time_out";

export function UpdateDateTimeModal({
  booking,
  openUpdateDateTimeModal,
  setOpenUpdateDateTimeModal,
  ...props
}) {
  const queryClient = useQueryClient();

  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const [isUpdateError, setIsUpdateError] = useState(false);
  const [isUpdateSucces, setIsUpdateSucces] = useState(false);
  const { set } = useTimeout(() => {
    setIsUpdateError(() => false);
    setIsUpdateSucces(() => false);
    setOpenUpdateDateTimeModal(() => false);
  }, 3000);

  const { handleSubmit, control } = useForm({
    defaultValues: {
      booking_date: `${booking?.booking_date}`,
      booking_time: `${booking?.booking_time}`,
    },
  });
  const onSubmit = async (data) => {
    try {
      await updateBookingAsync({ id: booking.id, ...data });
    } catch (error) {}
  };
  const {
    isLoading: isUpdateLoading,
    isSuccess: d,
    mutateAsync: updateBookingAsync,
    isError: s,
    error: updateError,
  } = useMutation("update_additional_information_modal", {
    mutationFn: updateBookingFunction,
    onSuccess: (event) => {
      setIsUpdateSucces((s) => true);
      setDisableUserInterface((s) => false);
      queryClient.invalidateQueries(["fetched-booking"]);
      set();
    },
    onMutate: (event) => {
      setDisableUserInterface((l) => true);
    },
    onError: (event) => {
      setIsUpdateError((e) => true);
      setDisableUserInterface((e) => false);
    },
  });
  return (
    <Modal
      {...props}
      size="lg"
      id="date-time"
      position="center"
      show={openUpdateDateTimeModal}
      onClose={() => setOpenUpdateDateTimeModal(false)}
    >
      <Modal.Header
        className={cn("bg-yellow-200 border-none", {
          "bg-gray-200": booking?.booking_completed,
          "bg-red-200": booking?.booking_canceled,
        })}
      >
        <span className="text-slate-800 font-bold text-2xl">Date time</span>
      </Modal.Header>
      <Modal.Body className="bg-white rounded-md" color="#fff">
        <div>
          <p className="text-sm">
            Trust Reload logistics services to transport your items safely.
          </p>
        </div>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="my-4 flex flex-col">
            <span className="text-sm font-bold text-gray-400 mb-2">
              Booking date
            </span>
            <Controller
              control={control}
              name="booking_date"
              render={({ field }) => (
                <DatePicker
                  className={cn(
                    "text-sm w-full rounded-md text-gray-700 border-gray-200 focus:ring-yellow-400 focus:bg-white focus:border-yellow-400 py-3 px-4 disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-75"
                  )}
                  disabled={disableUserInterface}
                  dateFormat="yyyy-MMM-dd"
                  timeFormat="HH:mm"
                  minDate={Date.now()}
                  showPopperArrow={false}
                  closeOnScroll={true}
                  selected={field.value}
                  onChange={(date) =>
                    field.onChange(moment(date).format("yyyy-MM-DD"))
                  }
                  onFocus={(e) => (e.target.readOnly = true)}
                />
              )}
            />
          </div>
          <div className="custom_datepicker_width my-4 flex flex-col">
            <span className="text-sm text-gray-400 font-bold mb-2">
              Booking time
            </span>
            <Controller
              control={control}
              name="booking_time"
              render={({ field }) => (
                <DatePicker
                  timeFormat="HH:mm"
                  dateFormat="HH:mm"
                  timeCaption="Time"
                  disabled={disableUserInterface}
                  className={cn(
                    "text-sm rounded-md w-full  disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-75 text-gray-700 border-gray-200 focus:ring-yellow-400 focus:bg-white focus:border-yellow-400 py-3 px-4",
                    {
                      // "border-red-500 bg-red-50 bg-opacity-100": isDateTimeError,
                    }
                  )}
                  minDate={Date.now()}
                  timeIntervals={15}
                  showTimeSelect={true}
                  showTimeSelectOnly={true}
                  showPopperArrow={false}
                  selected={new Date(Date.parse(`2024-06-05 ${field.value}`))}
                  onChange={(time) =>
                    field.onChange(moment(time).format("HH:mm:ss"))
                  }
                  onFocus={(e) => (e.target.readOnly = true)}
                />
              )}
            />
          </div>

          <div>
            {isUpdateError && (
              <ErrorAlert
                className="mb-4"
                secondaryText={`${updateError.response.data.message} ${updateError.message}`}
              />
            )}
            {isUpdateSucces && (
              <SuccessAlert
                secondaryText={`Booking date time updated successfully`}
                className="mb-4 w-full"
              />
            )}
          </div>
          <div>
            <div className="flex flex-row items-center justify-end gap-3">
              <Button disabled={disableUserInterface} className="w-auto">
                {isUpdateLoading ? (
                  <div className="w-full h-full flex justify-center items-center">
                    <ThreeDots visible width="40" height="20" color="#000" />
                  </div>
                ) : (
                  "Update"
                )}
              </Button>
            </div>
          </div>
        </form>
      </Modal.Body>
    </Modal>
  );
}
